<template>
  <v-app id="inspire">
    <!-- Menu-->

    <v-btn fab bottom right fixed @click="toggle_dark_mode">
    <v-icon>mdi-lightbulb</v-icon>
  </v-btn>

    <!-- Main body-->

    <v-main style="padding: 64px 0px 0px;" class="change-font">
      <!--  -->
      <v-container class="fluid">
        <div class=" row mt-16 justify-center">
          <div class="col sm=10 col-sm-10 col-md-8 col-lg-6 col-xl-4">
            <h1>Xipu Li</h1>
            <p></p>
            <p>
              Software Engineer | Tech Investor
            </p>
            <div id="p1">
              Greetings! I'm a junior from Fordham University majoring in
              Finance ('fuh-nance'). I love the technology industry and have
              always been amazed by what can be achieved using a few lines of
              code. In December 2020, I decided to break into tech.
            </div>
            <br />
            <div id="p1">
              Programming is a powerful tool to solve hard problems. As an
              aspiring software engineer, I'm excited to leverage my coding
              abiliity to build things that make people's lives easier. And as a
              tech investor, I want to empower businesses that share the same
              mission.
            </div>
            <br />
            <div id="p1">
              I believe that greatness can be achieved through
              <mark>freedom</mark>, <mark>integrity</mark>, and
              <mark>persistence</mark>. These three core values have been
              guiding my life and investment decisions.
            </div>
          </div>
        </div>
      </v-container>

      <v-sparkline
        :value="value"
        :gradient="gradient"
        :smooth="radius || false"
        :padding="padding"
        :line-width="width"
        :stroke-linecap="lineCap"
        :gradient-direction="gradientDirection"
        :fill="fill"
        :type="type"
        :auto-line-width="autoLineWidth"
        auto-draw
        class="ma-n16"
      ></v-sparkline>

      <div class="mt-16">
        <div class=" row my-16 justify-center">
          <div
            class="col sm=10 col-sm-10 col-md-8 col-lg-6 col-xl-4 just-line-break"
          >
            <v-card elevation="21" class="mb-16">
              <v-card-title class="text-center justify-center py-6">
                <h1 class="font-weight-bold change-font">
                  My Values
                </h1>
              </v-card-title>

              <v-tabs v-model="tab" background-color="transparent" grow>
                <v-tab v-for="tab in tabs" :key="tab">
                  {{ tab }}
                </v-tab>
              </v-tabs>

              <v-tabs-items v-model="tab">
                <v-tab-item v-for="text in texts" :key="text">
                  <v-card flat>
                    <v-card-text>{{ text }}</v-card-text>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-card>
            <div class="text-center">
              At the age of 14, I came to the United States
            </div>
            <br />
            <div class="text-center">So the journey begins</div>
            <br />

            <v-timeline align-top>
              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    High school stuff..
                  </v-card-title>
                  <v-card-text class="text--primary pt-3">
                    <p>
                      Learned a bit about coding but my English was not good
                      enough to understand the docs. To improve my English, I
                      started binging on books. But I enjoy building things more
                      than just consuming stuff built by others -- same goes for
                      books. So I built my own empire in a book, and published
                      it on Amazon.
                    </p>
                    <v-btn
                      class="mx-0"
                      outlined
                      onclick="window.open('https://www.amazon.com/dp/B071CM68D7/ref=cm_sw_em_r_mt_dp_x50-Fb7EVH02V', '_blank')"
                    >
                      Check it Out
                    </v-btn>
                  </v-card-text>
                </v-card>
              </v-timeline-item>
              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    Pure fur-nance stuff
                  </v-card-title>
                  <v-card-text class="text--primary pt-3">
                    <p>
                      Freshman year in college. Got my first equity research
                      internship at a small hedge fund in New York. Honed my
                      qualitative analytical skills by researching public
                      companies.
                      <br />
                      <br />
                      I was curious about how a company operates on a day-to-day
                      basis, so I wanted to intern at a Private Equity firm to
                      learn more about that. After sending 500+ cold emails
                      asking for an internship, I finally got a yes.
                    </p>
                  </v-card-text>
                </v-card>
              </v-timeline-item>

              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    Analyst Intern
                  </v-card-title>
                  <v-card-text class="text--primary pt-3">
                    <p>
                      At HPS Investment Partners. I discovered that I could
                      write some scripts to save 90% of my time. For example, I
                      was asked to create a folder and several subfolders for
                      each project we had (about hundreds of them). Dreaded by
                      the task, I wrote a script that automatically did this for
                      me under seconds.
                      <br />
                      <br />
                      This was when I discovered the power of coding. What did I
                      do with the time saved? Umm... Reddit.
                    </p>
                  </v-card-text>
                </v-card>
              </v-timeline-item>

              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    Tech Investing
                  </v-card-title>
                  <v-card-text class="text--primary pt-3">
                    <p>
                      In summer 2020, I interned at a hedge fund under a
                      portfolio manager who's very good at investing in SaaS
                      companies. I had a chance to speak with CEOs and software
                      engineers of some tech companies to assist with the
                      primary research.
                      <br />
                      <br />
                      After the summer, I interned at a VC/Growth Equity fund
                      that invests in Enterprise Software companies. I was
                      amazed by what today's startups are doing to transform our
                      lives tomorrow.
                    </p>
                    <v-btn
                      class="mx-0"
                      outlined
                      onclick="window.open('https://moicandroic.home.blog/', '_blank')"
                    >
                      My Investing Blog
                    </v-btn>
                  </v-card-text>
                </v-card>
              </v-timeline-item>

              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    First software
                  </v-card-title>
                  <v-card-text class=" text--primary pt-3">
                    <p>
                      Currently, I'm interning at C2 Energy Capital. I made a
                      little program that can figure out the solar energy rate
                      for each solar projects given assumptions like tax
                      incentives, project terms, etc. to hit the required
                      investment return rate.
                    </p>
                  </v-card-text>
                </v-card>
              </v-timeline-item>

              <v-timeline-item fill-dot small class="mt-16">
                <v-card>
                  <v-card-title class="secondary--text primary change-font">
                    December 2020
                  </v-card-title>
                  <v-card-text class=" text--primary pt-3">
                    <p>
                      I had an epipheny that tech is the future. Also as an
                      investor, it's going to be my edge if I can understand
                      tech companies from ground up. I started taking the CS61A
                      course taught at UC Berkeley and absolutely loved every
                      minute of it. And I wanted to build things using the
                      computer science knowledge.
                    </p>
                    <br />
                    <br />
                    <p>
                      I networked with many people who got into tech from
                      unconventional backgrounds. After witnessing so many
                      stories, I know there's a possibility. It's time to break
                      into tech.
                    </p>
                  </v-card-text>
                </v-card>
              </v-timeline-item>
            </v-timeline>
          </div>
        </div>
      </div>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { title: "Home", icon: "mdi-view-dashboard" },
        { title: "About", icon: "mdi-forum" },
        { title: "Values", icon: "mdi-forum" },
        { title: "Portfolio", icon: "mdi-forum" },
        { title: "Weird Flex", icon: "mdi-forum" },
      ],
      fab: false,
      reveal: false,
      width: 1,
      radius: 10,
      padding: 1,
      lineCap: "round",
      value: [0, 2, 5, 3, 6, 10, 8, 15, 11, 21, 17, 25],
      gradientDirection: "top",
      fill: false,
      type: "trend",
      autoLineWidth: true,
      tab: null,
      tabs: ["Freedom", "Integrity", "Persistence"],
      texts: [
        "Innovation never happens under coercions. I usually find myself creating amazing things when there's a great level of freedom in my life. I will build things that enable freedom to people and have invested in companies that do the same, such as Shopify, Amazon, Uber and Sea Limited.",
        "Trust is scarce in our post-scarcity world. I believe honesty is a paramount principle. When facing difficult challenges in life, integrity is what separates the great from the good. I want to be a leader who is transparent about things and can make ethical decisions when facing conflicts of interests.",
        "A high IQ cannot get you anywhere if you don't have the tenacity to weather the storm. I've personally experienced the power and benefit of being persistent. This is perhaps the simplest recipe to success in life, relationships and careers.",
      ],
      timelineColors: [
        {
          color: "red lighten-2",
          icon: "mdi-star",
        },
        {
          color: "purple darken-1",
          icon: "mdi-book-variant",
        },
        {
          color: "green lighten-1",
          icon: "mdi-airballoon",
        },
        {
          color: "indigo",
          icon: "mdi-buffer",
        },
      ],
    };
  },
  methods: {
    toggle_dark_mode: function() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    },
  },
  theme: {
    dark: true,
  },
  mounted() {
    const theme = localStorage.getItem("dark_theme");
    if (theme) {
      if (theme === "true") {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    } else if (
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
      this.$vuetify.theme.dark = true;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    }
  },
  watch: {
    top(val) {
      this.bottom = !val;
    },
    right(val) {
      this.left = !val;
    },
    bottom(val) {
      this.top = !val;
    },
    left(val) {
      this.right = !val;
    },
  },
};
</script>
<style>
@import url("https://cdn.jsdelivr.net/gh/tonsky/FiraCode@1.207/distr/fira_code.css");
.change-font {
  font-family: "JetBrains Mono";
  font-weight: 600;
  font-style: normal;
}
#p1 {
  font-weight: 400;
}
#just-line-break {
  white-space: pre-line;
}
.v-tooltip__content {
  font-family: "JetBrains Mono";
  font-weight: 400;
  font-style: normal;
}

::selection {
  color: black;
  background: orange;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateZ(0px);
}

.foreground {
  margin-top: auto;
  margin-bottom: 50px;
  transform-origin: 0;
  transform: translateZ(3px);
}

.foreground h1 {
  font-size: 36px;
}
mark {
  background-color: black;
  color: white;
}
</style>
